@use '../util' as *;

.access-bg {
  background-color: var(--very-dk-blue-opac);
}

.access {
  padding-top: 80px;
  padding-bottom: 80px;
  color: var(--white);
  font-family: var(--font-headings);

  @include breakpoint(large) {
    text-align: left;
  }
  
  &__signup {
    @include breakpoint-down(medium) {
      margin-bottom: rem(10);
    }
  } 

  &__email {
    width: 90%;
    border: none;
    border-radius: rem(2);
    padding: rem(10);

    @include breakpoint-down(medium) {
      margin-bottom: rem(10);
    }

    @include breakpoint(large) {
      margin-bottom: rem(10);
      width: 100%;
      padding: rem(15);
    }
  }

  &__submit {
    width: 90%;
    color: var(--white);
    font-weight: bold;
    background-color: var(--bright-blue);
    border: none;
    border-radius: rem(2);
    padding: rem(10);
    cursor: pointer;

    @include breakpoint(large) {
      width: 40%;
      padding: rem(15);
    }
  }
}