.access-bg {
  background-color: var(--very-dk-blue-opac);
}

.access {
  padding-top: 80px;
  padding-bottom: 80px;
  color: var(--white);
  font-family: var(--font-headings);
}
@media (min-width: 71.875em) {
  .access {
    text-align: left;
  }
}
@media (max-width: 71.8125em) {
  .access__signup {
    margin-bottom: 0.625rem;
  }
}
.access__email {
  width: 90%;
  border: none;
  border-radius: 0.125rem;
  padding: 0.625rem;
}
@media (max-width: 71.8125em) {
  .access__email {
    margin-bottom: 0.625rem;
  }
}
@media (min-width: 71.875em) {
  .access__email {
    margin-bottom: 0.625rem;
    width: 100%;
    padding: 0.9375rem;
  }
}
.access__submit {
  width: 90%;
  color: var(--white);
  font-weight: bold;
  background-color: var(--bright-blue);
  border: none;
  border-radius: 0.125rem;
  padding: 0.625rem;
  cursor: pointer;
}
@media (min-width: 71.875em) {
  .access__submit {
    width: 40%;
    padding: 0.9375rem;
  }
}